.animated {
  z-index: -1;
}
.animated::before {
  content: "";
  width: 170px;
  height: 191px;
  /* box-shadow: 0 0 0px 0px #E19E13; */
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 14px;
  animation: blowing 3s infinite alternate;
  z-index: -1;
}
.animated::after {
  content: "";
  width: 170px;
  height: 191px;
  /* box-shadow: 0 0 0px 0px #E19E13; */
  opacity: 0;
  position: absolute;
  top: 0px;
  left: 0;
  border-radius: 28px;
  animation: blowing_after 5s infinite alternate;
  z-index: -1;
}
/* .animated{
    animation: blowing 3s infinite alternate;
    box-shadow: 0 0 20px 10px #E19E13;
} */
@keyframes blowing {
  0% {
    box-shadow: 10px -10px 25px -7px #e58c25;
    opacity: 0.7;
  }
  100% {
    box-shadow: 6px -6px 30px -7px #dd5852;
    opacity: 0.9;
  }
}
@keyframes blowing_after {
  0% {
    box-shadow: -4px 6px 30px -10px #00c2ff;
    opacity: 0.6;
  }
  100% {
    box-shadow: -6px 10px 35px -10px #7854bf;
    opacity: 0.8;
  }
}
